import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';

import {HttpClient } from '@angular/common/http';
import { environment } from '@env/environment';
import { PFAvailabilityDto, AvailabilityWithMatchesDto } from '../model/availability';
import { PlayingFieldSetupDto } from '../model/playing-field';

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: 'root'
})
export class WorkerService {

  constructor(private http: HttpClient) { }

  setPFAvailability(availability: PFAvailabilityDto, id: number): Observable<any> {
  return this.http.post<Observable<any>>(`${API_URL}/worker/playingField/${id}/setAvailability`, availability);
  }

  setPFSetup(id, playingFieldSetup: PlayingFieldSetupDto) {
    let formData = new FormData();
    formData.append("description", playingFieldSetup.description);
    formData.append("teamSize", playingFieldSetup.teamSize.toString());
    formData.append("pfPhoto", playingFieldSetup.pfPhoto);
    formData.append("name", playingFieldSetup.name);
    return this.http.put(
      `${API_URL}/worker/playingField/${id}/setup`,
      formData
    );
  }

  deletePFAvailability(id) {
    return this.http.delete(`${API_URL}/worker/playingField/playingFieldAvailabilities/${id}`)
  }
}
