import { Time } from "@angular/common";
import { MatchWithLocationDto } from './playing-field';

export class PFAvailabilityDto {
  openDatePF: Date;
  closeDatePF: Date;
  openTimePF: Time;
  closeTimePF: Time;
  matchTime: number;

  constructor(openDatePF, closeDatePF, openTimePF, closeTimePF, matchTime) {
    this.openDatePF = openDatePF;
    this.closeDatePF = closeDatePF;
    this.openTimePF = openTimePF;
    this.closeTimePF = closeTimePF;
    this.matchTime = matchTime;
  }
}

export interface AvailabilityWithMatchesDto {
  availabilityId: number;
  fromDate: Date;
  toDate: Date;
  matchesDto: MatchDto[];
}
export interface MatchDto {
  id: number;
  matchFrom: Date;
  matchTo: Date;
  isBooked: boolean;
  size: number;
  maxSize: number;
  isPrivate: boolean;
  ownerId: number;
  ownerUsername: string;
  editable: boolean;
  code: string;
  isCodeFilled: boolean;
  phoneNumber: string;
  surname: string;
  email: string;
}

export interface InviteDto {
  id: number;
  senderUsername: string;
  senderId: number;
  matchWithLocationDto: MatchWithLocationDto
}
